.formulario-principal .campo-escolha {
  background-color: var(--branco);
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 20px;
  padding: 10px;
  display: flex;
}

.campo-escolha__legenda {
  color: var(--preto);
  text-align: center;
  text-transform: capitalize;
  font-size: 1.5rem;
}

.campo-escolha__texto-escolha {
  font-size: 1.15rem;
}

.campo-escolha__escolha {
  margin: 0 calc(50% - 13px);
  display: block;
}

.formulario-principal .campo-escolha .campo-escolha__texto-escolha {
  max-width: 110px;
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  margin: 10px;
  padding: 4px;
  transition: all .5s;
  display: flex;
}

.formulario-principal .campo-escolha .campo-escolha__texto-escolha:before {
  content: "\a ";
  white-space: pre-wrap;
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  box-shadow: 2px 2px 5px 1px var(--shadow);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  margin-bottom: 8px;
  transition: all .3s linear;
  -webkit-animation-delay: .2s;
  animation-delay: .2s;
  display: block;
}

.formulario-principal .campo-escolha .campo-escolha__texto-escolha:hover {
  max-width: 155px;
}

.formulario-principal .campo-escolha .campo-escolha__texto-escolha:hover:before {
  width: 150px;
  height: 150px;
  box-shadow: 0px 0px 6px 1px var(--botao-base);
}

.formulario-principal {
  background-color: var(--branco-escuro);
  text-align: center;
  padding: 20px 5vw;
}

.formulario-principal__botao {
  color: var(--branco);
  background-color: var(--botao-base);
  border: 3px solid var(--branco);
  border-radius: 10px;
  padding: 10px;
  font-size: 1.25rem;
  transition: all .5s;
}

.formulario-principal__botao:hover {
  background-color: var(--botao-ativo);
}

